@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  height: 100%;
  background: #fff;
  color: #000;
  font-family: 'Roboto', sans-serif;
}

.container {
  max-width: 1240px;
  width: 1240px;
  margin: 5% auto;
  margin-bottom: 50px;
  box-shadow: 0 1px 5px 0 rgba(0, 0, 0, .25);
  position: relative;
  z-index: 1;
  border-radius: 12px;
  background-color: #fff;
}

.no-margin {
  margin: 0;
}

@media screen and (max-width: 768px) {
  .container {
    max-width: 100vw;
  }
}
.search {
    padding: 5%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    max-width: 100%;
    overflow-x: hidden;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    font-family: 'Roboto', sans-serif;
}

.search__row {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    max-width: 100%;
}

.search__input {
    height: 30px;
    min-width: 50vw;
    border: none;
    box-shadow: none;
    border-radius: 2px;
    padding: 1rem 3rem;
    background-color: #fff;
    color: #4e4c4a;
    letter-spacing: -1px;
    border: 1px solid #4e4c4a;
    border-radius: 4px;
    font-size: 20px;
    letter-spacing: 1rem;
    font-family: 'Roboto', sans-serif;
}

.search__input:focus {
    background: #fff;
    box-shadow: 0 0 5px  #09b109c7;
    border: 1px solid  #09b109c7;
    outline: 0;
}

.search__button {
    padding: 1rem 3rem;
    position: relative;
    display: inline-block;
    text-align: center;
    border: 0;
    border-radius: 4px;
    background: #09b109c7;
    color: #fff;
    letter-spacing: .2px;
    line-height: 1.5;
    min-height: 1em;
    cursor: pointer;
    text-decoration: none;
    -webkit-appearance: none;
            appearance: none;
    overflow: visible;
    font-size: 20px;
    border: 1px solid #09b109c7;
    font-family: 'Roboto', sans-serif;
}

@media screen and (max-width: 768px) {
    .search__row {
        margin: 0 auto;
        margin-top: 50px;
    }

    .search__input {
        width: 100vw;
        letter-spacing: 0.5rem;
        ;
    }
}
.centers {
    display: -webkit-flex;
    display: flex;
    margin: 0 3%;
    -webkit-flex-direction: row;
            flex-direction: row;
    overflow: hidden;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
}

.center {
    display: -webkit-flex;
    display: flex;
    overflow: hidden;
    padding: 2% 5% 2% 2%;
    min-height: 200px;
    width: 300px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: auto auto 5% auto;
    box-shadow: 0 1px 5px 0 rgba(0, 0, 0, .25);
    position: relative;
    z-index: 1;
    border-radius: 12px;
    background-color: #fff;
}

.result-count {
    box-shadow: none;
}

.center__title-primary {
    font-size: 1.8rem;
    font-weight: bolder;
}

.center__title-secondary {
    font-size: 1.1rem;
    font-weight: bold;
}

.center__tiles {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.center__tiles>.tile {
    margin-left: 0;
}

.card__age-limit {
    bottom: 0;
    right: 0;
    text-align: right;
    padding: 2% 5% 2% 5%;
    position: absolute;
    font-size: 0.7rem;
    color: #fff;
    background: #085078;
    /* fallback for old browsers */
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #85D8CE, #085078);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    border-end-end-radius: 12px;
}


@media screen and (max-width: 768px) {
    .centers {
        width: 100vw;
        margin: 0 auto;
        margin-bottom: 50%;
    }

    .center {
        width: 100%;
        margin: 1% 5%;
        padding: 2% 15% 2% 5%;
    }

    .card__age-limit {
        padding: 2% 3% 2% 4%;
        font-size: 0.5rem;
    }

    .result-count {
        margin-left: 5%;
    }
}
.tile {
    padding: 0.4rem;
    font-size: 0.7rem;
    display: -webkit-flex;
    display: flex;
    border-radius: 5px;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin: 5%;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: #09b109c7;
    color: #fff;
}

